* {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f0f2f6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.TopButtonsMenu {
  width: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 70px 0 50px 0;
}

.TopButtonsMenu button {
  font-size: 12px;
  background-color: #ffffff;
  border-radius: 5px;
  border: 1px solid #ffffff;
  cursor: pointer;
  width: 48%;
  margin: 10px 0;
  padding: 10px;
  height: 50px;
  font-weight: 700;
  color: #38b86f;
  outline: none;
}
.ListButton {
  display: none;
}

@media (min-width: 1000px) {
  .ListButton {
    display: block;
    margin-right: 10px;
    cursor: pointer;
  }
}
.CardButton {
  display: none;
}

@media (min-width: 1000px) {
  .CardButton {
    display: block;
    margin-left: 10px;
    cursor: pointer;
  }
}
.SwitchView {
  width: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin: 20px 0 0 0;
}

.SwitchViewTitle {
  font-weight: 700;
  color: #282828;
  font-size: 16px;
}

.SwitchViewButtonsWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.Item {
  border-radius: 5px;
  box-shadow: 0 0 4px rgba(92, 95, 100, .2);
  padding: 20px;
  background-color: #ffffff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 20px 0;
}

.Title, .Attributes, .Description {
  padding: 8px 0;
}

.Index {
  font-size: 14px;
  font-weight: 900;
  color: #38b86f;
  padding: 4px 0;
}

.Title {
  font-size: 18px;
  font-weight: 900;
  color: #282828;
}

.Attributes {
  font-size: 14px;
  font-weight: 500;
  color: #282828;
}

.Description {
  font-size: 14px;
  font-weight: 400;
  color: #747474;
}


.Items {
  width: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  margin: 10px 0 30px 0;
}
.Wrapper {
  width: 80%;
  margin: 20px 0;
}

.BottomFormTitle {
  font-weight: 700;
  color: #282828;
  font-size: 16px;
}

.BottomForm {
  margin: 30px 0 70px 0;
}

.BottomForm label {
  display: block;
  margin: 10px 0;
  font-size: 14px;
  font-weight: 400;
  color: #747474;
}

.BottomForm input, .BottomForm button {
  outline: none;
  padding: 14px;
  width: 100%;
  color: #282828;
  font-weight: 500;
  margin: 5px 0 0 0;
  border-radius: 5px;
  font-size: 12px;
  height: 50px;
  background-color: #ffffff;
}

.BottomForm button {
  border: 1px solid #ffffff;
  font-weight: 700;
  margin: 20px 0 0 0;
  cursor: pointer;
}

.BottomForm ::-webkit-input-placeholder {
  font-weight: 300;
  color: #747474;
}

.BottomForm ::-ms-input-placeholder {
  font-weight: 300;
  color: #747474;
}

.BottomForm ::placeholder {
  font-weight: 300;
  color: #747474;
}
.App {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

